@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
body {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.page-header {
  position: relative;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-width: 320px;
}
.page-header__logo {
  position: absolute;
  bottom: -86px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}
@media (min-width: 768px) {
  .page-header__logo {
    bottom: auto;
    top: 0;
  }
}
@media (min-width: 1200px) {
  .page-header {
    width: 1200px;
  }
  .page-header__logo-image {
    width: 188px;
    height: 96px;
  }
}
.main-nav {
  position: relative;
}
.main-nav__toggle {
  position: absolute;
  width: 23px;
  height: 22px;
  border: none;
  background: transparent;
}
.main-nav--closed .main-nav__toggle {
  bottom: -37px;
  right: 20px;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
.main-nav--opened .main-nav__toggle {
  top: 16px;
  right: 20px;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  left: -3px;
  width: 30px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--opened .site-list__item {
    display: block;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.site-list__link {
  display: block;
  box-sizing: border-box;
  width: 100%;
  padding: 15px 20px;
  font-size: 18px;
  line-height: 25px;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #81b3d2;
  box-shadow: 0 1px 0 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 0 rgba(255, 255, 255, 0.2);
}
.site-list__link:not(.site-list__link--active):hover,
.site-list__link:not(.site-list__link--active):focus {
  background-color: #669ec0;
}
.site-list__link:not(.site-list__link--active):active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
@media (min-width: 768px) {
  .site-list {
    display: flex;
    flex-wrap: wrap;
  }
  .site-list__item {
    width: 20%;
  }
  .site-list__item:nth-child(4n+2) {
    margin-right: 20%;
  }
  .site-list__link {
    font-size: 14px;
    color: #000000;
    background-color: transparent;
    box-shadow: none;
  }
  .site-list__link--active {
    color: #766357;
  }
  .site-list__link--active:hover,
  .site-list__link--active:focus {
    color: #604e43;
  }
  .site-list__link--active:active {
    color: rgba(118, 99, 87, 0.3);
  }
  .site-list__link:not(.site-list__link--active):hover,
  .site-list__link:not(.site-list__link--active):focus {
    color: #81b3d2;
    background-color: transparent;
  }
  .site-list__link:not(.site-list__link--active):active {
    color: rgba(0, 0, 0, 0.3);
    background-color: transparent;
  }
}
.page-main {
